      this.rulemodal=true
      this.shopmodal=true   
    },  
    //ç¡®è®¤  
    confirm(){
      this.modalstate=true 
      wx.showToast({
        title: 'å¡«å†™æˆåŠŸ',
        icon: 'success',
        duration: 1000
      })
    },
    //è§„åˆ™
    showrule(){
      this.rulemodal=false;
    },
    //å…‘æ¢å•†å“æ“ä½œ
    choseshop(sl){
      if(this.userdata.phonestatue==0){
        this.userdata.phone==''       
        this.tel();
      }else{
        wx.showModal({
          title: 'æç¤º',
          content: 'ç¡®å®šå…‘æ¢'+sl.name+'å—ï¼Ÿ',
          //æ”¹æˆè¿™ä¸ªæ ·å¼å¯ä»¥æŠŠthisä¼ å…¥
          success:(res)=>{
            if (res.confirm) {
              this.useshop(sl);
            } else if (res.cancel) {
              //console.log('ç”¨æˆ·ç‚¹å‡»å–æ¶ˆ')
            }
          }
