    },
    //è‡ªåŠ¨è·å–æ‰‹æœºæŒ‰é’®
    getPhone(e){
      if(e.mp.detail.iv==null){
        wx.showToast({
          title: 'å‘œå‘œï¼Œè¢«æ‹’ç»äº†',
          icon: 'none',
          duration: 2000
        })
        console.log("è·å–æ‰‹æœºå¤±è´¥")
      }else{
        var etc={
          session:this.session,
          encryptedData:e.mp.detail.encryptedData,
          iv:e.mp.detail.iv,
          phone:this.userdata.phone
        };
        this.upPhone(etc);
        }
    },
    //æ‰‹åŠ¨æäº¤æ‰‹æœº
    getPhone2(){
      //åˆ¤æ–­æ‰‹æœºå·æ­£åˆ™
      if(!(/^1[34578]\d{9}$/.test(this.userdata.phone))){
        wx.showToast({
          title: "æ‰‹æœºå·å¡«é”™å•¦ï¼ï¼å¯ä»¥è¯•è¯•è‡ªåŠ¨å™¢~",
          icon: 'none',
          duration: 2000
        })
      }else{
        var etc={session:this.session,encryptedData:'',iv:'',phone:this.userdata.phone}
        this.upPhone(etc);    
      }

    },
    //ä¸Šä¼ æ‰‹æœºè¯·æ±‚
    upPhone(etc){
      var url='https://myzhbit.cn/getup/web/?r=init/getnumber';
      var data=etc;
      
      this.getrequest(url,data)
      .then((res)=>{
        this.confirm();
      })
      .then(()=>{
        this.getuserdata();
      })
    },
    //è·å–ç”¨æˆ·ä¿¡æ¯
    getuserdata(){
      var url='https://myzhbit.cn/getup/web/index.php?r=user/userdata';
      var data={session:this.session};
      this.getrequest(url,data)
      .then((res)=>{
        this.userdata.jifen=res.data.data.shopscore;
        this.userdata.phonestatue=res.data.data.phonestatue;
      })
    },
    //è·å–å•†å“ä¿¡æ¯
    getshoplist(){
      var url='https://myzhbit.cn/getup/web/?r=shop/display';
      var data='';
      this.getrequest(url,data)
      .then((res)=>{
        this.shoplist=res.data.data;
      })
    },
    //è·å–ä¼˜æƒ åˆ¸
    gettick(){
      var url='https://myzhbit.cn/getup/web/?r=shop/coupon';
      var data={session:this.session};
      this.getrequest(url,data)
      .then((res)=>{
        this.tick=res.data.data;
      })      
    },
    //ä½¿ç”¨ä¼˜æƒ åˆ¸
    usetick(tkk){
      var that = this;
      wx.showToast({
        title: "",
        icon: 'loading',
        mask:'true',
        duration: 1500,
        complete: function() {
          that.usecoupon(tkk)
          .then((res)=>{
            wx.showToast({
              title: res.data.data,
              icon: 'success',
              duration: 2000
            })
          })
          .then(()=>{
            that.gettick();
          })            
        }
      })      
    },
    //å…‘æ¢ä¼˜æƒ åˆ¸è¯·æ±‚
    usecoupon(tkk){
      var that=this;
      return new Promise(function (resolve, reject) {
        var url='https://myzhbit.cn/getup/web/?r=shop/usecoupon';
        var data={session:that.session,id:tkk};
        that.getrequest(url,data)
        .then((res)=>{
          resolve(res);
        })
      })
    },
    //å…‘æ¢å•†å“
    useshop(sl){
      var that = this;
      wx.showToast({
        title: "",
        icon: 'loading',
        mask:'true',
        duration: 1000,
        complete: function() {
          that.buycoupon(sl)
          .then((res)=>{
            
            wx.showToast({
              title: res.data.data,
              icon: 'none',
              duration: 2000
            })
          })
          .then(()=>{
            that.getshoplist();
            that.getuserdata();
            that.gettick();
          })
          console.log('å…‘æ¢å•†å“')               
        }
      })      

      /*æ—§é€»è¾‘å¤„ç†
      if(Number(this.userdata.jifen)>=Number(sl.much) && 0<Number(sl.left)){
        this.userdata.jifen=this.userdata.jifen-sl.much;
        sl.left=sl.left-1;
        wx.showToast({
          title: 'å…‘æ¢æˆåŠŸ',
          icon: 'success',
          duration: 1000
        })
      }else if(0>=Number(sl.left)){
        wx.showToast({
          title: 'ç¤¼å“æ•°é‡ä¸è¶³ï¼',
          icon: 'none',
          duration: 1500
        })
      }else if(Number(this.userdata.jifen)<Number(sl.much)){
        wx.showToast({
          title: 'ç§¯åˆ†ä¸è¶³ï¼',
          icon: 'none',
          duration: 1500
        })
      }*/
    },
    //å…‘æ¢å•†å“è¯·æ±‚
    buycoupon(sl){
      var that=this;
      return new Promise(function (resolve, reject) {
        var url='https://myzhbit.cn/getup/web/?r=shop/buycoupon';
        var data={session:that.session,cid:sl.id,name:sl.name,score:sl.score};
        that.getrequest(url,data)
        .then((res)=>{
          resolve(res);
        })
      })      
    }
  },
  onShow(){
      this.getStorage("3rd_session")
      .then((res)=>{
        this.session=res.data;
      })
      .then(()=>{
        this.getuserdata();
      })
      .then(()=>{
        this.getshoplist();
        this.gettick();
      })

  },
  created () {
  
  }

}
</script>

<style lang="scss" scoped>
